.QuizList {
    display: flex;
    justify-content: center;
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
    background: linear-gradient(90deg, #448AFF 0%, #2979FF 50%, #1A237E 100%);
}
.QuizList h1{
    color: #fff;
}
.QuizList ul{
    list-style: none;
    margin: 0;
    padding: 0;
}
.QuizList li{
    margin-bottom: 10px;
}
.QuizList li a{
    text-decoration: none;
    color: #fff;
}
.QuizList li a:hover{
    color: #363f54;
}

